@import './colors.scss';

.card{

    //FUNCTIONALITY
    perspective: 150rem; //perspective always on the parent
    -moz-perspective: 150rem;
    position: relative;
    height: 52rem; //for perspective 3d flip back 

    &__side{ //child element get the perspective view
        height: 52rem; // for both side
        transition: all .8s ease;
        position: absolute;
        top:0;
        left: 0;
        width: 100%;
        backface-visibility: hidden;
        border-radius: 4px;
        overflow: hidden; //because the image of &--1 overflowing the parent/border-radius in the corner
        box-shadow: 0 1.5rem 4rem rgba($color-black, .15);

        &--front{
            background-color: $color-white;
        }

        &--back{
            transform: rotateY(180deg);

            &-1{
                background-image: linear-gradient(to right bottom, $color-card1-light, $color-card1-dark);
            }
        }
    }

    &:hover &__side--front{
        transform: rotateY(-180deg);   
    }

    &:hover &__side--back{
        transform: rotateY(0);   
        object-fit: cover;
    }

    //FRONT SIDE STYLING
    &__picture{
        background-size: cover;
        background-position: center;
        height: 22rem;
        background-blend-mode: screen;
        -webkit-clip-path: polygon(0 0, 100% 0, 100% 85%, 0 100%);
        // clip-path: polygon(0 0, 100% 0, 100% 75%, 0 100%);

        &--1{
            background-image: linear-gradient(to right bottom, $color-card1-light, $color-card1-dark),
                url('../img/future1.jpg');
        }
    }

    &__heading{
        font-size: 2.8rem;
        font-weight: 300;
        text-transform: uppercase;
        text-align: right;
        color: $color-white;
        position: absolute;
        top: 12rem;
        right: 2rem;
        width: 75%;
    }

    &__heading-span{
        padding: 1rem 1.5rem;
        box-decoration-break: clone;
        -webkit-box-decoration-break: clone; //make two different entities

        &--1{
            background-image: linear-gradient(to right bottom, 
            rgba($color-card1-light, .85), 
            rgba($color-card1-dark, .85));
        }
    }

    &__details{
        padding: 3rem;

        ul{
            list-style: none;
            width: 80%;
            margin: 0 auto;

            li{
                text-align: center;
                font-size: 1.5rem;
                padding: 1rem;
                
                &:not(:last-child){
                    border-bottom: 1px solid $color-grey-light-2;
                }
            }
        }
    }

    //BACK SIDE STYLING
    &__player-wrapper{
        -webkit-clip-path: polygon(0 0, 100% 0, 100% 94%, 0 85%);
        clip-path: polygon(0 0, 100% 0, 100% 94%, 0 85%);
    }

    &__trailerVideo{
        width: 100%;
    }




}
